.ProductDetails {
  background-color: rgb(255, 255, 255);
  width: 100vw;
  max-width: 100%;
  padding: 6vmax;
  box-sizing: border-box;
  display: flex;
}

.ProductDetails > div {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 2vmax;
  box-sizing: border-box;
  border: 1px solid white;
}

.ProductDetails > div:last-child {
  align-items: flex-start;
}

.CarouselImage {
  width: 30vmax;
}

.detailsBlock-1 > h2 {
  color: rgb(0, 0, 0);
  font: 600 1.6vmax;
}

.detailsBlock-1 > p {
  color: rgba(54, 54, 54, 0.582);
  font: 200 0.6vmax;
}

.detailsBlock-2 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-top: 1px solid rgba(0, 0, 0, 0.205);
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  width: 70%;
  padding: 1vmax 0;
}

.detailsBlock-2-span {
  font: 200 0.8vmax;
  color: rgba(0, 0, 0, 0.699);
}

.detailsBlock-3 {
  width: 70%;
}

.detailsBlock-3 > h1 {
  color: rgb(0, 0, 0);
  font: 400 1.8vmax;
  font-size: 1.8vmax;
  margin: 1vmax 0;
}
.detailsBlock-3-1 {
  display: flex;
  align-items: center;
}
.detailsBlock-3-1-1 {
  /* width: 10vmax; */
  /* margin-right: 2vw; */
}

.detailsBlock-3-1-1 > button {
  border: 1px solid transparent;
  background-color: var(--pink);
  padding: 0.5vmax;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
}
.detailsBlock-3-1-1 > button:hover {
  background-color: rgb(255, 255, 255);
  color: var(--pink);
  border: 1px solid var(--pink);
}

.detailsBlock-3-1-1 > input {
  border: none;
  padding: 0.5vmax;
  width: 3vmax;
  text-align: center;
  outline: none;
  font: 400 0.8vmax;
  color: rgba(0, 0, 0, 0.74);
}

.detailsBlock-3-1 > button:last-child {
  border: 1px solid transparent;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
  background-color: var(--pink);
  font: 500 0.7vmax;
  border-radius: 60px;
  padding: 0.5vmax 2vmax;
  margin: 1vmax;
  outline: none;
}

.detailsBlock-3-1 > button:last-child:hover {
  background-color: rgb(255, 255, 255);
  color: var(--pink);
  border: 1px solid var(--pink);
}

.detailsBlock-3 > p {
  border-top: 1px solid rgba(0, 0, 0, 0.205);
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  padding: 1vmax 0;
  color: rgba(0, 0, 0, 0.651);
  font: 400 1vmax;
  margin: 1vmax 0;
}

.detailsBlock-4 {
  color: rgba(0, 0, 0, 0.897);
  font: 500 1.2vmax;
}

.detailsBlock-4 > p {
  color: rgba(0, 0, 0, 0.534);
  font: 300 0.8vmax;
}

.submitReview {
  border: 1px solid transparent;
  background-color: var(--pink);
  font: 500 0.7vmax;
  border-radius: 60px;
  padding: 0.6vmax 2vmax;
  margin: 1vmax 0;
  color: white;
  cursor: pointer;
  transition: all 0.5s;
  outline: none;
}
.submitReview:hover {
  background-color: rgb(255, 255, 255);
  color: var(--pink);
  border: 1px solid var(--pink);
  /* transform: scale(1.1); */
}

.submitDialog {
  display: flex;
  flex-direction: column;
}
.submitDialogTextArea {
  border: 1px solid rgba(0, 0, 0, 0.082);
  margin: 1vmax 0;
  outline: none;
  padding: 1rem;
  font: 300 1rem;
}

.reviewsHeading {
  color: #000000be;
  font: 500 1.4vmax;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.226);
  padding: 1vmax;
  width: 20vmax;
  margin: auto;
  margin-bottom: 4vmax;
}
.reviews {
  display: flex;
  overflow: auto;
}

.reviewCard {
  flex: none;
  box-shadow: 2px 2px 6px 1px rgba(0, 0, 0, 0.321);
  background-color: #ffedf1;
  border: 1px solid transparent;
  width: 30vmax;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1vmax;
  padding: 3vmax;
}
.reviewCard > img {
  object-fit:cover;
  width: 20%;
  /* height: 10%; */
  border-radius: 100%;
  color: var(--pink);
}
.reviewCard > p {
  color: rgb(0, 0, 0);
  font: 600 0.9vmax;
}
.reviewCardComment {
  color: rgb(0, 0, 0);
  font: 300 0.8vmax;
}

.noReviews {
  font: 400 1.3vmax;
  text-align: center;
  color: rgb(0, 0, 0);
}

@media screen and (max-width: 600px) {
  .ProductDetails {
    flex-direction: column;
    height: unset;
  }

  .ProductDetails > div:last-child {
    align-items: center;
  }

  .detailsBlock-1 > h2 {
    font-size: 2.8vmax;
    text-align: center;
  }

  .detailsBlock-1 > p {
    text-align: center;
    font-size: 1vmax;
  }

  .detailsBlock-2 {
    justify-content: center;
  }
  .detailsBlock-2 > span {
    font-size: 1.5vmax;
  }

  .detailsBlock-3 > h1 {
    font: 700 3vmax;
    text-align: center;
  }

  .detailsBlock-3-1 {
    flex-direction: column;
  }

  .detailsBlock-3-1-1 {
    padding: 2vmax 0;
  }
  .detailsBlock-3-1-1 > button {
    padding: 1.2vmax;
    width: 4vmax;
  }

  .detailsBlock-3-1-1 > input {
    padding: 1.5vmax;
    width: 6vmax;
    font: 400 1.8vmax;
  }

  .detailsBlock-3-1 > button:last-child {
    font: 500 1.7vmax;
    padding: 1.5vmax;
    width: 20vmax;
    margin: 3vmax 0;
  }

  .detailsBlock-3 > p {
    padding: 2.5vmax 0;
    text-align: center;
    font: 400 2vmax;
  }

  .detailsBlock-4 {
    font: 500 2.5vmax;
  }

  .detailsBlock-4 > p {
    font: 300 1.8vmax;
  }

  .submitReview {
    font: 500 1.7vmax;
    padding: 1.5vmax;
    width: 20vmax;
    margin: 3vmax 0;
  }

  .reviewCard > p {
    font: 600 3vw;
  }
  .reviewCardComment {
    font: 300 5vw;
  }
}
