.shippingContainer {
  width: 100vw;
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.shippingBox {
  background-color: white;
  width: 40vw;
  height: 90vh;
  box-sizing: border-box;
  overflow: hidden;
}

.shippingHeading {
  text-align: center;
  color: rgb(0, 0, 0);
  /* font: 400 1vmax; */
  font-size: 1.4vmax;
  font-weight: 400;
  padding: 1vmax;
  border-bottom: 1px solid var(--pink);
  width: 92%;
  margin: auto;
}

.shippingForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* width: 100vw; */
  margin: auto;
  padding: 1vmax;
  justify-content: space-evenly;
  height: 80%;
  transition: all 0.5s;
}

.shippingForm > div {
  display: flex;
  width: 100%;
  align-items: center;
}

.shippingForm > div > input,
.shippingForm > div > select {
  margin-top: 10px;
  padding: 0.5vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid var(--pink);
  border-radius: 50px;
  font: 300 0.9vmax;
  outline: none;
}

.shippingForm > div > svg {
  position: absolute;
  transform: translateX(1vmax) translateY(0.4vmax);
  font-size: 1.6vmax;
  color: var(--pink);
}

.shippingBtn {
  border: 1px solid transparent;
  border-radius: 60px;
  background-color: var(--pink);
  color: white;
  font: 300 1vmax;
  width: 100%;
  padding: 1vmax;
  cursor: pointer;
  transition: all 0.5s;
  outline: none;
  margin: 2vmax;
}

.shippingBtn:hover {
  background-color: rgb(255, 255, 255);
  color: var(--pink);
  border: 1px solid var(--pink);
}

@media screen and (max-width: 600px) {
  .shippingBox {
    width: 100vw;
    height: 95vh;
  }

  .shippingHeading {
    font: 400 6vw "Roboto";
    padding: 5vw;
  }

  .shippingForm {
    padding: 11vw;
  }

  .shippingForm > div > input,
  .shippingForm > div > select {
    padding: 5vw 10vw;
    font: 300 4vw cursive;
  }

  .shippingForm > div > svg {
    font-size: 6vw;
    transform: translateX(3vw);
  }

  .shippingBtn {
    font: 300 4vw "Roboto";
    padding: 4vw;
  }
}
